import React, { Component } from "react";
import { connect } from "react-redux";
import ReactTooltip from "react-tooltip";
import { Field, reduxForm } from "redux-form";
import { getLokasi } from "../../actions/lokasiAction";
import { showModal } from "../../actions/ModalAction";
import { getListMutasiBarang } from "../../actions/mutasiBarangAction";
import { deleteIndex } from "../../deleteLocalStorage";
import { getToday, SkeletonLoading } from "../../getDate";
import GlobalTabel from "../../GlobalComponent/GlobalTabel";
import { renderField, renderFieldSelect } from "../../renderField";
import validasiHeadMutasiBarang from "../../Validations/validasiHeadMutasiBarang";

class HeadMutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lokasi: false,
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
      columns: [
        {
          dataField: "Lihat Detail",
          isDummyField: true,
          text: "Lihat Detail",
          formatter: (row) => {
            return (
              <span>
                <i className="fa fa-chevron-down"></i>
              </span>
            );
          },
        },
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_beli",
          text: "Kat Harga",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar",
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
        },
        {
          dataField: "kadar_beli",
          text: "Harga",
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger"
                  onClick={() =>
                    deleteIndex(
                      "mutasiBarang",
                      rowIndex,
                      this.props.dispatch,
                      getListMutasiBarang()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => console.log("EDIT")}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }
  componentDidMount() {
    this.props.dispatch(getLokasi());
    this.props.change("tanggal", getToday);
    this.props.change("noMutasi", localStorage.getItem("noFaktur"));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group d-none">
            <Field
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              label="Nomor Mutasi"
              type="text"
              name="noMutasi"
              className="form-control"
              readOnly
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              type="date"
              label="Tanggal"
              id="tanggal"
              name="tanggal"
              component={renderField}
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              label="Bon Mutasi"
              type="text"
              id="bonMutasi"
              name="bonMutasi"
            />
          </div>
          <div className="col-md-3 form-group">
            <Field
              id="kategori"
              label="Kategori"
              name="kategori"
              component={renderFieldSelect}
              normalize={(value) => value && value.toUpperCase()}
              options={this.state.listKategori.map((list) => {
                let data = {
                  value: list.nama,
                  name: list.nama,
                };
                return data;
              })}
            ></Field>
          </div>
          <div className="col-md-3 form-group">
            {this.props.lokasi ? (
              <Field
                label="Lokasi"
                component={renderFieldSelect}
                normalize={(value) => value && value.toUpperCase()}
                type="text"
                id="lokasi"
                name="lokasi"
                onKeyPress={(e) => {
                  e.key === "Enter" && e.preventDefault();
                }}
                options={this.props.lokasi.map((list) => {
                  let data = {
                    value: list.kode_lokasi,
                    name: list.nama_lokasi,
                  };
                  return data;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Lokasi" />
            )}
          </div>
          <div className="col-md-3 form-group">
            <Field
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
              label="Keterangan"
              type="text"
              id="keterangan"
              name="keterangan"
            />
          </div>

          <div className="col-lg-3"></div>
          <div className="col-lg-4"></div>
          <div className="col-lg-4 col-md-4 mb-3"></div>
          <div className="col-lg-4 col-md-4 mb-3">
            <div className="text-right">
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => this.props.dispatch(showModal())}
              >
                TAMBAH BARANG
                <i className="fa fa-plus ml-3"></i>
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <GlobalTabel
              keyField="id"
              data={this.props.listBarang}
              columns={this.state.columns}
            />
          </div>
          <div className="col-lg-12 col-md-3 mb-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.onSend}
                type="button"
                onClick={this.props.handleSubmit}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                SIMPAN
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

HeadMutasiBarang = reduxForm({
  form: "HeadMutasiBarang",
  enableReinitialize: true,
  validate: validasiHeadMutasiBarang,
})(HeadMutasiBarang);
export default connect((state) => {
  return {
    listBarang: state.provinsi.mutasiBarang,
    lokasi: state.provinsi.lokasi,
  };
})(HeadMutasiBarang);
