import React, { Component, lazy, Suspense } from "react";

import { getListMutasiBarang } from "../actions/mutasiBarangAction";
import Swal from "sweetalert2";
import { connect } from "react-redux";
import { reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import ReactTooltip from "react-tooltip";
import {
  finishSend,
  getBruto,
  getGross,
  getNetto,
  getPkg,
  getQty,
  progressSend,
} from "../actions/LogicAction";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";
import HeadMutasiBarang from "./Component/HeadMutasiBarang";
import Skeleton from "react-loading-skeleton";
import HeaderCard from "../HeaderCard";
import { AxiosMasterPost } from "../Axios";

const FormMutasiBarang = lazy(() => import("./Component/FormMutasiBarang"));
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);

const maptostate = (state) => {
  return {
    listBarang: state.provinsi.mutasiBarang,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    sumGross: state.perhitungan.sumGross,
    onSend: state.provinsi.onSend,
  };
};

class MutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lokasi: [],
      kodeJenis: "",
      kodeJenisTujuan: "",
      noMutasi: "",
      kodeLokasi: "",
      tanggal: "",
      keterangan: "",
      kategori: "",
      noBon: "",
      columns: [
        {
          dataField: "Lihat Detail",
          isDummyField: true,
          text: "Lihat Detail",
          formatter: (row) => {
            return (
              <span>
                <i className="fa fa-chevron-down"></i>
              </span>
            );
          },
        },
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_beli",
          text: "Kat Harga",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar",
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
        },
        {
          dataField: "kadar_beli",
          text: "Harga",
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  type="button"
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger"
                  onClick={() => this.hapusLocal(rowIndex)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
    };
  }

  hapusLocal(data) {
    let hasil = JSON.parse(localStorage.getItem("mutasiBarang")) || [];
    hasil.splice(data, 1);
    localStorage.setItem("mutasiBarang", JSON.stringify(hasil));
    this.props.dispatch(getListMutasiBarang());
  }

  componentDidMount() {
    this.props.dispatch(getListMutasiBarang());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("mutasiBarang"));
    this.props.dispatch(getNetto("mutasiBarang"));
    this.props.dispatch(getPkg("mutasiBarang"));
    this.props.dispatch(getQty("mutasiBarang"));
    this.props.dispatch(getGross("mutasiBarang"));
  }

  handleSubmit(data) {
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
    });
    var supp1 = data.kodeBarangTujuan
      ? data.kodeBarangTujuan
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result = supp1.split("|");
    this.setState({
      kodeJenisTujuan: result[0],
    });
    var barang1 = JSON.parse(localStorage.getItem("mutasiBarang")) || [];
    var simpan = {
      bruto: parseFloat(data.bruto).toFixed(3),
      bruto2: parseFloat(data.brutoTujuan).toFixed(3),
      kadar_cetak: parseFloat(data.kadar),
      kadar_cetak2: parseFloat(data.kadarTujuan),
      kadar_beli: parseFloat(data.kadar_beli),
      kadar_beli2: parseFloat(data.kadar_beliTujuan),
      kadar_jual: parseFloat(data.kadar_jual),
      kadar_jual2: parseFloat(data.kadar_jualTujuan),
      netto: parseFloat(data.netto).toFixed(3),
      netto2: parseFloat(data.nettoTujuan).toFixed(3),
      pkg: parseFloat(data.pkg).toFixed(3),
      pkg2: parseFloat(data.pkgTujuan).toFixed(3),
      qty: 1,
      qty2: 1,
      gross: (parseFloat(data.bruto) + parseFloat(data.pkg)).toFixed(3),
      gross2: (
        parseFloat(data.brutoTujuan) + parseFloat(data.pkgTujuan)
      ).toFixed(3),
      kode_jenis: result_explode[0].toUpperCase(),
      kode_jenis2: result[0].toUpperCase(),
      kode_kategori: data.kategori,
      kode_kategori2: data.kategoriTujuan,
    };
    barang1.push(simpan);
    localStorage.setItem("mutasiBarang", JSON.stringify(barang1));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("FormMutasiBarang")))
      .then(() => this.props.dispatch(getListMutasiBarang()))
      .then(() => window.location.reload());
  }
  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submitMutasiBarang(data) {
    if (localStorage.getItem("mutasiBarang") === null) {
      warning("Mohon Tambahkan Barang Yang akan dipindahkan");
      return false;
    }
    var total = JSON.parse(localStorage.getItem("mutasiBarang")) || [];
    var pkg =
      total &&
      total
        .map((list) => parseFloat(list.pkg))
        .reduce((a, b) => a + b, 0)
        .toFixed(3);
    var total_bruto =
      total &&
      total
        .map((list) => parseFloat(list.bruto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3);
    var total_netto =
      total &&
      total
        .map((list) => parseFloat(list.netto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3);
    var total_netto2 =
      total &&
      total
        .map((list) => parseFloat(list.netto2))
        .reduce((a, b) => a + b, 0)
        .toFixed(3);
    var gross = parseFloat(total_bruto) + parseFloat(pkg);
    var simpan = {
      no_mutasi: this.props.noFaktur,
      no_bon: data.bonMutasi,
      tgl_mutasi: data.tanggal,
      lokasi_asal: data.lokasi,
      lokasi_tujuan: data.lokasi,
      total_gross: gross,
      total_pkg: pkg,
      total_bruto: total_bruto,
      total_netto: total_netto,
      total_netto2: total_netto2,
      keterangan: data.keterangan,
      deskripsi: data.kategori,
      detail_barang: JSON.parse(localStorage.getItem("mutasiBarang")),
    };
    console.log(JSON.stringify(simpan));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("mutasiBarang"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kode_jenis,
        ticket.bruto,
        ticket.kadar_jual,
        ticket.netto,
        "<=====>",
        ticket.kode_jenis,
        ticket.kadar_jual2,
        ticket.netto2,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = [
      "No",
      "Kode Jenis",
      "Bruto",
      "Harga (%)",
      "Netto",
      "<====>",
      "Kode Jenis 2",
      "Harga 2",
      "Netto 2",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("mutasi-stock/simpan", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(reset("HeadMutasiBarang")))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => window.location.reload())
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor Bon",
              data.noMutasi,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "MUTASI BARANG",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("mutasiBarang"))
          .then(() => this.props.dispatch(getListMutasiBarang()))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            this.setState({
              kodeJenis: "",
              kodeJenisTujuan: "",
              noMutasi: "",
              kodeLokasi: "",
              tanggal: "",
              keterangan: "",
              kategori: "",
              noBon: "",
            })
          )
          .then(() => this.forceUpdate())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => console.log(err))
          .then(() => this.props.dispatch(finishSend()))
      );
    this.props.dispatch(getListMutasiBarang());
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Mutasi Barang" />
              <div className="card-body">
                <div className="row">
                  <div className="col-lg-12">
                    <HeadMutasiBarang
                      onSubmit={(data) => this.submitMutasiBarang(data)}
                      onSend={this.props.onSend}
                      noFaktur={this.props.noFaktur}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={5} />}
            >
              <FormMutasiBarang onSubmit={(data) => this.handleSubmit(data)} />
            </Suspense>
          }
          title="Tambah Data Mutasi Barang"
        />
      </div>
    );
  }
}

export default connect(maptostate, null)(MutasiBarang);
